  background: #f5745a;
}

.menu ul > li:hover ul, .menu ul > li:active ul, .menu ul > li:focus ul {
  display: block;
}

.menu ul > li.sfHover > a, .menu ul > li.current-menu-item > a, .menu ul > li.current-menu-parent > a {
  color: #fff;
  background: #f5745a;
}

@media screen and (min-width: 1024px) {
  .menu {
    position: static;
    left: auto;
    overflow: inherit;
    background: none;
    width: auto;
    padding: 0;
  }
  .menu ul > li {
    display: inline-block;
    position: relative;
  }
  .menu ul > li:hover ul, .menu ul > li:active ul, .menu ul > li:focus ul {
    top: 40px;
  }
  .menu ul > li.sfHover ul {
    top: 40px;
  }
  .menu ul ul {
    top: 100%;
    position: absolute;
    background: #fff;
  }
  .menu ul ul li a:hover, .menu ul ul li a:active, .menu ul ul li a:focus {
    color: #fff;
    background: #f5745a;
  }
}

@media screen and (max-width: 1024px) {
  .menu-mobile--active .menu {
    left: 0;
    box-shadow: 0 0 100px rgba(17, 17, 17, 0.5);
  }
}

/* Breadcrumb
----------------------------------------------------------------------------------------------------*/
.breadcrumb {
  margin: 0 0 20px;
}

/* Entry
----------------------------------------------------------------------------------------------------*/
.entry::after {
  clear: both;
  content: "";
  display: table;
}

.entry__title,
.page__title {
  font-weight: 900;
  text-transform: uppercase;
}

.entry__title > a,
.page__title > a {
  text-decoration: none;
}

.entry__title > a:hover, .entry__title > a:active, .entry__title > a:focus,
.page__title > a:hover,
.page__title > a:active,
.page__title > a:focus {
  text-decoration: underline;
}

.entry__summary {
  margin-bottom: 50px;
}

.entry__content::after {
  clear: both;
  content: "";
  display: table;
}

.entry__content ul {
  list-style: disc inside none;
  margin: 0 0 20px;
}

.entry__content ul li {
  margin: 0 0 10px;
}

.entry__content ol {
  list-style: decimal inside none;
  margin: 0 0 20px;
}

.entry__content ol li {
  margin: 0 0 10px;
}

.entry__content img {
  display: block;
  max-width: 100%;
  height: auto;
}

.entry__content .alignright,
.entry__content .alignleft {
  max-width: 50%;
}

@media screen and (min-width: 768px) {
  .entry__content .alignright,
  .entry__content .alignleft {
    max-width: 100%;
  }
}

.entry__content .gform_wrapper ul {
  list-style: none;
}

.entry__content .searchform input {
  display: inline;
}

.entry__summary::after {
  clear: both;
  content: "";
  display: table;
}

.entry__img {
  display: block;
  max-width: 100%;
  height: auto;
}

/* Comments
 ----------------------------------------------------------------------------------------------------*/
.comment-list .children {
  margin: 0 0 0 76px;
}

.comment-body {
  margin: 0 0 10px;
}

.comment-meta {
  position: relative;
  min-height: 56px;
  padding: 0 0 0 76px;
  margin: 0 0 10px;
}

.avatar {
  position: absolute;
  left: 0;
  top: 0;
}

.comment-content p:last-child {
  margin-bottom: 0;
}

/* Widgets
 ----------------------------------------------------------------------------------------------------*/
.widget-area img {
  display: block;
  max-width: 100%;
  height: auto;
}

.widget {
  margin: 0 0 20px;
}

.widget::after {
  clear: both;
  content: "";
  display: table;
}

.widget_categories .current-cat > a,
.widget_categories .current-cat .current-cat a,
.widget_categories .current-cat-parent > a,
.widget_categories .current-cat-parent .current-cat a {
  font-weight: 700;
}

.widget_pages .current_page_ancestor > a,
.widget_pages .current_page_ancestor .current_page_item a,
.widget_pages .current_page_parent > a,
.widget_pages .current_page_parent .current_page_item a {
  font-weight: 700;
}

/* Searchform
----------------------------------------------------------------------------------------------------*/
.searchform {
  position: relative;
}

.searchform__field[type=search] {
  padding-right: 30px;
}

.searchform__submit {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
}

.searchform__submit .icon {
  vertical-align: -15%;
}

.home-slider__wrapper {
  width: 100%;
  overflow: hidden;
}

.home-slider {
  width: 60%;
  margin: auto;
}

.home-slider .slick-list {
  overflow: visible;
}

.home-slider__item {
  text-align: center;
}

.home-slider__item img {
  display: inline-block;
}

.home-slider__nav__wrapper {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 25%;
  width: 50%;
  transform: translateY(-50%);
}

.home-slider__nav {
  text-align: center;
  font-family: Bebas, sans-serif;
  background: #f00;
}

@media screen and (min-width: 768px) {
  .home-slider__nav .slick-list {
    height: 190px !important;
    padding: 60px 0 45px !important;
  }
  .home-slider__nav .slick-slide {
    padding: 5px 0;
  }
}

.home-slider__nav__item {
  height: 60px;
  line-height: 60px;
  color: #fff;
  text-transform: uppercase;
  transition: all .3s ease-in-out;
  text-decoration: none;
}

@media screen and (min-width: 768px) {
  .home-slider__nav__item {
    font-size: 35px;
  }
  .home-slider__nav__item.slick-current {
    font-size: 70px;
  }
}

